%module {
  background: $white;
  box-shadow: 0 1px 1px 0 rgba($black, 0.1);
}

%module-h-title {
  .title {
    @include pin-left($bar-title-width);
    @include css-variable(color, text-light);
    padding: 12px 5px 0 0;
    font-size: 12px;
    text-transform: uppercase;
    text-align: right;
  }
}
